.logo:hover,
.logo:focus {
  opacity: 0.8;
}

.logo:active {
  opacity: 0.6;
}

.logo--no-text {
  margin: 0;
}

.logo__img {
  width: 190px;
  height: 40px;
}

.logo__img--only-text {
  width: 128px;
  height: 24px;
}

@media (min-width: $tablet-width) {
  .logo__img {
    width: 174px;
    height: 59px;
  }

  .logo__img--only-text {
    width: 128px;
    height: 24px;
  }
}

@media (min-width: $desktop-width) {
  .logo__img {
    width: 203px;
    height: 60px;
    margin-top: 7px;
  }

  .logo__img--only-text {
    width: 128px;
    height: 24px;
    margin: 0;
  }
}
