    left: 0;
    width: 40px;
    height: 40px; }
  .choice__check {
    top: -5px;
    width: 40px;
    height: 40px; }
  [type="radio"]:checked + .choice__radio::before {
    top: 10px;
    left: 10px;
    width: 18px;
    height: 18px; }
  [type="checkbox"]:checked + .choice__check::before {
    top: 10px;
    left: 8px; } }
